.navbar {
  height: 4em;
  width: 100%;
  position: fixed !important;
  z-index: 10; }

.leftNav {
  position: absolute;
  left: 1em; }

.rightNav {
  position: absolute;
  right: 1em;
  font-size: 1.3em; }

.navSocialMediaIcons {
  display: none; }

.bgBlue {
  background-color: white;
  box-shadow: 0px 6px 22px -9px rgba(0, 0, 0, 0.75); }

.navbarLogo {
  display: inline-block;
  width: 3em;
  height: 3em; }

@media (min-width: 576px) {
  .leftNav {
    left: 2em; }
  .rightNav {
    right: 2em; } }

@media (min-width: 992px) {
  .navSocialMediaIcons {
    display: inline-block; }
  .navbar-logo {
    width: 3.5em;
    height: 3.5em; }
  .navbar {
    height: 4.5em; } }
