.container {
  position: relative;
  max-width: 100%;
  margin: 0;
  padding: none; }
  .container::after {
    content: '';
    position: absolute;
    width: 3px;
    background-color: #a373f9;
    top: 20px;
    bottom: 0;
    left: calc(12% + 1px);
    margin-left: -3px; }
    @media (min-width: 768px) {
      .container::after {
        left: calc(12% + 1px); } }
    @media (min-width: 992px) {
      .container::after {
        left: calc(37% + 1px); } }

.title {
  font-weight: 200;
  color: #a373f9; }

.event {
  position: relative;
  margin: 0;
  padding: 1em 0;
  width: 100%; }
  .event::after {
    /* circle thing on line */
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    left: calc(12% - 13px);
    background-color: white;
    border: 3px solid #a373f9;
    top: 20px;
    border-radius: 50%;
    z-index: 1; }
    @media (min-width: 768px) {
      .event::after {
        left: calc(12% - 13px); } }
    @media (min-width: 992px) {
      .event::after {
        left: calc(37% - 13px); } }

.eventTime {
  font-family: 'Roboto Slab';
  margin-bottom: 0.5em;
  font-size: 1.2em;
  text-align: left; }
  @media (min-width: 992px) {
    .eventTime {
      text-align: right;
      font-size: 1em; } }

.eventBody {
  font-size: 1.1em;
  font-weight: 600;
  padding: 0;
  margin: 0; }
