.menuContainer,
.navButton {
  z-index: 10; }

.navButton,
.menu {
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.1); }

.navButton {
  position: fixed;
  bottom: 2em;
  right: 2em;
  display: flex;
  width: 4em;
  height: 4em;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  color: #a373f9;
  background-color: white;
  border: none;
  transition: background-color 0.5s ease-in-out, transform 0.2s ease-in-out; }

.navButton:hover {
  transform: scale(1.05); }

.navButtonToggled {
  color: white !important;
  background-color: #a373f9 !important; }

.navButton:focus {
  outline: 0; }

.navIcon {
  font-size: 1.5em; }

.menuContainer {
  position: fixed;
  bottom: 2em;
  right: 6em;
  padding-right: 1em; }

.menu {
  height: 100%;
  padding: 1em;
  border-radius: 1.5em;
  background-color: white;
  transition: transform 0.2s ease-in-out; }

.menu:hover {
  transform: scale(1.03); }

.menu ul {
  list-style: none;
  padding: 0.5em;
  margin: 0;
  text-align: center; }

.menu ul li {
  font-size: 1.5em;
  font-family: 'Roboto Slab';
  padding-top: 0.25em;
  opacity: 1;
  transition: transform 0.25 ease-in-out, opacity 0.25s; }

.menu ul li:first-child {
  padding-top: 0; }

.menu ul li:hover {
  transform: scale(1.05);
  color: #c596fe; }

.menu ul li:active {
  color: #a373f9; }
